import { createApp } from 'vue'
import App from './App.vue'

import  "@/assets/style.scss";
import  "@/assets/fonts.css";

createApp(App).mount('#app')


let navMenu = document.querySelector(".nav__menu")
let navInfo = document.querySelector(".nav__info")
let navList = document.querySelector(".nav__list")

navMenu.addEventListener("click", () => {
    navInfo.classList.toggle("active")
})

let catalogTxt = document.querySelectorAll(".catalog__txt")

catalogTxt.forEach(catalogTxts => {
    catalogTxts.addEventListener("click", () => {
        catalogTxts.classList.toggle("active")
    })  
});

let whatCards = document.querySelector(".what__cards")

function anim(entries) {
    // console.log(entries);
    if (entries[0].isIntersecting) {
        whatCards.classList.add("active")
    } else {
        whatCards.classList.remove("active")
    }
}

let what = new IntersectionObserver(anim, {threshold: 0.7})
what.observe(whatCards)


let catalog小ard = document.querySelectorAll(".catalog__card")

catalog小ard.forEach(catalog小ards => {
    
    function anim(entries) {
        // console.log(entries);
        if (entries[0].isIntersecting) {
            catalog小ards.classList.add("activ")
        } else {
            catalog小ards.classList.remove("activ")
        }
    }
    let catalog小ard = new IntersectionObserver(anim, {threshold: 0.5})
    catalog小ard.observe(catalog小ards)
});

